import Model from "./Model.js";

export class Main {
    constructor() {
        this.model = new Model();

        this.creatBoard();
    }

    creatBoard() {
        console.log('createBoard');
    }


    update() {
        this.model.addvanceTurn();
        if(this.model.isResult()){
            if(this.model.isFirstMove()) {
                console.log("先手勝利");
            } else {
                console.log("後手勝利");
            }
            return;
        }
    }
}
